<template>
  <div class="container">
    <div class="container-img-ctn" @click="toPage">
      <div :style="{width: wreathWh() + 'px', height: wreathWh() + 'px'}" class="flower-round">
        <div id="timeDiv" class="home-time"><span>{{ days }}</span> days <span>{{ hoursStr }}</span> hours
          <span>{{ minutesStr }}</span> minutes <span>{{ secondsStr }}</span> seconds
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import HelloWorld from '@/components/HelloWorld.vue'
import DrawHeartCanvas from '@/js/DrawHeartCanvas'
import PetalTest from '@/views/PetalTest.vue'
import Test from '@/views/Test.vue'
import $ from 'jquery'

@Options({
  components: {
    Test,
    PetalTest,
    HelloWorld
  }
})
export default class Home extends Vue {
  visualWidth = document.documentElement.clientWidth
  visualHeight = document.documentElement.clientHeight
  canvasHeight = 0
  canvasWidth = 0
  days = 0
  hours = 0
  minutes = 0
  seconds = 0
  hoursStr = ''
  minutesStr = ''
  secondsStr = ''

  mounted (): void {
    $('#timeDiv').css('top', (this.wreathWh() * 0.470) + 'px')
    this.timeStart()
  }

  private toPage (): void {
    this.$router.push({ path: '/sms' })
  }

  private wreathWh (): number {
    let wh = 0
    if (this.visualWidth > this.visualHeight) {
      wh = this.visualHeight
    } else {
      wh = this.visualWidth
    }
    return wh
  }

  private timeStart () {
    const together = new Date()
    together.setFullYear(2012, 7, 17)
    together.setHours(12)
    together.setMinutes(0)
    together.setSeconds(0)
    together.setMilliseconds(0)
    this.timeElapse(together)
    setInterval(() => {
      this.timeElapse(together)
    }, 500)
  }

  private timeElapse (date: any) {
    const current = Date()
    this.seconds = (Date.parse(current) - Date.parse(date)) / 1000
    this.days = Math.floor(this.seconds / (3600 * 24))
    this.seconds = this.seconds % (3600 * 24)
    this.hours = Math.floor(this.seconds / 3600)
    if (this.hours.toString().length < 2) {
      this.hoursStr = '0' + this.hours
    } else {
      this.hoursStr = this.hours + ''
    }
    this.seconds = this.seconds % 3600
    this.minutes = Math.floor(this.seconds / 60)
    if (this.minutes.toString().length < 2) {
      this.minutesStr = '0' + this.minutes
    } else {
      this.minutesStr = this.minutes + ''
    }
    this.seconds = this.seconds % 60
    if (this.seconds.toString().length < 2) {
      this.secondsStr = '0' + this.seconds
    } else {
      this.secondsStr = this.seconds + ''
    }
  }

  resizeCanvas (canvas: HTMLCanvasElement) {
    canvas.width = window.innerWidth
    canvas.height = window.innerHeight
  }
}
</script>

<style scoped>
@font-face {
  font-family: Digital-7;
  src: url("../assets/digital-7_mono.ttf");
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
}

.container-img-ctn {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  padding-top: 5%;
  background: #FEE5ED;
}

.flower-round {
  background: url('../assets/ic_m_flower_r.png') no-repeat;
  background-size: 100%;
  margin: 0 auto;
}

.home-time {
  position: relative;
  color: #A16A16;
}

.home-time span {
  font-family: Digital-7, serif;
  font-size: 1.5rem;
}
</style>
