<template>
  <div style="width: 100%;height: 100%">
    <template v-for="(item, index) in list" :key="item">
      <div class="flower-c" :style="{left: item.left, animation: 'move' + (index >= 10 ? index + 1 : index) + ' 5s both infinite'}">
        <div class="flower-g" :style="{background: item.color, height: item.gHeight}"></div>
        <div class="flower-h" :style="{color: item.color}">❤</div>
      </div>
    </template>
  </div>
</template>

<script lang="ts">
import { Vue } from 'vue-class-component'
import SmsCommon from '@/js/SmsCommon'
class DynamicPx {
  left: string
  gHeight: string
  color: string
  constructor (left: string, gHeight: string, color: string) {
    this.left = left
    this.gHeight = gHeight
    this.color = color
  }
}
export default class Test extends Vue {
  list = Array<DynamicPx>()

  mounted (): void {
    const ss = document.styleSheets[0]
    this.list.forEach((value, index, array) => {
      ss.removeRule(index)
    })
    const smsCommon = new SmsCommon()
    const clientWidth = document.body.clientWidth
    for (let i = 0; i < clientWidth / 80; i++) {
      this.list.push(new DynamicPx((i * 80) + 'px', this.randomNumberInRange(30, 120) + 'px', smsCommon.randomColorArr().replace('background: ', '')))
      ss.insertRule(this.getKeyFrames(i), i)
    }

    for (let i = 0; i < clientWidth / 80 >> 1; i++) {
      this.list.push(new DynamicPx(this.randomNumberInRange(0, clientWidth) + 'px', this.randomNumberInRange(30, 120) + 'px', smsCommon.randomColorArr().replace('background: ', '')))
      ss.insertRule(this.getKeyFrames(this.list.length), 0)
    }

    console.log(ss)
  }

  getKeyFrames (i: number): string {
    if (i === 10) i = 9
    const ra = this.randomNumberInRange(0, 10)
    return '@keyframes move' + i + ' {' +
      '0% { transform: rotate(-' + ra + 'deg); }\n' +
      '50% {transform: rotate(' + ra + 'deg);}\n' +
      '100% {transform: rotate(-' + ra + 'deg);}\n' +
      '}'
  }

  /**
   * 获取范围内的随机数
   * @param m
   * @param n
   * @private
   */
  private randomNumberInRange (min: any, max: any): number {
    return Math.random() * (max - min) + min
  }
}
</script>

<style scoped>
.flower-c {
  position: absolute;
  bottom: -30px;
  left: 200px;
  transform: rotate(40deg);
  transform-origin: 36px 113px;
  /*animation: move 3s both infinite;*/
}
.flower-g {
  background: #EE675C;
  height: 52px;
  width: 4px;
  position: relative;
  top: 0;
  left: 36px;
  bottom: 0;
  transform: rotate(180deg);
}
.flower-h {
  position: absolute;
  font-size: 80px;
  top: -85px;
  width: 0;
  height: 0;
}
</style>
