  visualHeight = document.body.clientHeight

  mounted (): void {
    // this.snowflake()
    let i = 0
    const ss = document.styleSheets[0]
    setInterval(() => {
      const wh = this.randomNumberRD(10, 40)
      this.htmlEl.push(new PetalData(wh, this.randomFlower()))
      ss.insertRule(this.getKeyFrames(i), 0)
      i++
    }, 200)

    let delI = 0

    const intval = setInterval(() => {
      setInterval(() => {
        $('#embed_id_' + delI + '').remove()
        delI++
      }, 200)
      clearInterval(intval)
    }, 16000)
  }

  private randomFlower (): any {
    const a = this.randomNumberRD(0, this.flowers.length).toFixed(0)
    return this.flowers[Number(a)]
  }

  /**
   * 获取范围内的随机数
   * @param m
   * @param n
   * @private
   */
  private randomNumberRD (min: any, max: any): number {
    return Math.random() * (max - min) + min
  }

  private randomRotateDirection (): number {
    return this.rotateDirection[Number(this.randomNumberRD(0, 1).toFixed(0))]
  }

  private getKeyFrames (i: number): string {
    const randomX = this.randomNumberRD(0, this.visualWidth)
    const randomY = this.randomNumberRD(0, this.visualHeight)
    const randomX2 = randomX + this.randomNumberRD(-200, 200)
    return '@keyframes rotateFall_' + i + ' {\n' +
      '  0% { transform: translate(' + this.visualWidth / 2 + 'px, ' + this.visualHeight / 2 + 'px) rotate3d(' + this.randomRotateDirection() + ',' + this.randomRotateDirection() + ',' + this.randomRotateDirection() + ',20deg);}\n' +
      '  100% { transform: translate(' + randomX + 'px, ' + randomY + 'px) rotate3d(' + this.randomRotateDirection() + ',' + this.randomRotateDirection() + ',' + this.randomRotateDirection() + ',1080deg)}\n' +
      '}'
    /* return '@keyframes rotateFall_' + i + ' {\n' +
      '  0% { transform: translate(' + randomX + 'px, ' + -40 + 'px) rotate3d(' + this.randomRotateDirection() + ',' + this.randomRotateDirection() + ',' + this.randomRotateDirection() + ',20deg);}\n' +
      '  100% { transform: translate(' + randomX2 + 'px, ' + (this.visualHeight + 80) + 'px) rotate3d(' + this.randomRotateDirection() + ',' + this.randomRotateDirection() + ',' + this.randomRotateDirection() + ',80deg)}\n' +
